@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;500;600&display=swap');

/* Sets the initial styling of all components */
* {
    margin: 0;
    padding: 0;
    border: 0;
    outline: 0;
    box-sizing: border-box;
    list-style: none;
    text-decoration: none;
}

/* The below is how you create a variable in CSS so we can apply it wherever we want, This is where you can change the color scheme of the whole project */
:root {
    --color-bg: rgb(17, 17, 17);
    --color-bg-variant: #1f1f1f;
    --color-primary: #c0c0c0;
    --color-primary-variant: #7e7777;
    --color-white: #ffffff;
    --color-light: #494949;
 /* These will be used to change the width of containers for different devices */   
    --container--width-lg: 75%;
    --container--width-md: 86%;
    --container--width-sm: 90%;
/* These will be all of the transitions that we use alot */
    --transition: all 400ms ease;
}

/* This will change the scrolling effect when scrolling down the page (Media Queries)*/
html {
    scroll-behavior: smooth;
}

/* This removes the scrollbar from the site entirely, we will only utilize mousewheel and touch */
::-webkit-scrollbar {
    display: none;
}

body {
    font-family: 'Poppins', sans-serif;
    background: var(--color-bg);
    color: var(--color-white);
    line-height: 1.7;
    background-image: linear-gradient(to top, black, black);
    background-position: center center;
    background-repeat: no-repeat;
    background-attachment: fixed;
    background-size: cover;
}

/* ======= General Styles ======= */
.container {
    width: var(--container--width-lg);
    margin: 0 auto;
}

h1,
h2,
h3,
h4,
h5 {
    font-weight: 500;
}

h1 {
    font-size: 2.5rem;
}

section {
    margin-top: 8rem;
}

section > h2,
section > h5 {
    text-align: center;
    color: var(--color-light);
}

section > h2 {
    color: var(--color-primary);
    margin-bottom: 3rem;
}

.text-light {
    color: var(--color-light);
}

a {
    color: var(--color-primary);
    transition: var(--transition);
}

a:hover {
    color: var(--color-white)
}

.btn {
    width: max-content;
    display: inline-block;
    color: var(--color-primary);
    padding: 0.9rem 1.5rem;
    border-radius: 0.4rem;
    cursor: pointer;
    border: 1px solid var(--color-primary);
    transition: var(--transition);
}

.btn:hover {
    background: var(--color-white);
    color: var(--color-bg);
    border-color: transparent;
    box-shadow: 0 0 15px 5px rgb(255, 0, 0);
}

.btn-primary {
    background: var(--color-primary);
    color: var(--color-bg);
}

img {
    display: block;
    width: 100%;
    object-fit: cover;
}

/* ========== MEDIA QUERY (MEDIUM DEVICES) =========== */
@media screen and (max-width: 1024px) {
    .container {
        width: var(--container--width-md);
    }

    section {
        margin-top: 6rem;
    }
}

/* ========== MEDIA QUERY (SMALL DEVICES) =========== */
@media screen and (max-width: 600px) {
    .container {
        width: var(--container--width-sm);
    }

    section > h2 {
        margin-bottom: 2rem;
    }
}